import React from "react";
import { connect } from "react-redux";
import {
  setCurrentPageAC,
  getUsersThunkCreator,
  unFollowThunk,
  followThunk,
} from "../../redux/users-reducer";
import Users from "./Users";
import Preloader from "../common/preloader/Preloader";
import { compose } from "redux";
import { getCurrentPage, getFollowingInProgress, getIsFetching, getPageSize, getTotalUsersCount, getUsers, getUsersSuperSelector } from "../../redux/users-selectors";


class UsersAPIContainer extends React.Component {
  componentDidMount() {
    let {currentPage, pageSize} = this.props;
    this.props.getUsersThunkCreator(currentPage, pageSize);
    
    /* this.props.toggleIsFetchingContainer(true);

    usersAPI.getUsers(this.props.currentPage, this.props.pageSize)
    .then((data) => {
        this.props.toggleIsFetchingContainer(false);
        this.props.setUssersContainer(data.items);
        this.props.setTotalUsersCountContainer(data.totalCount);
        console.log(data);
      }); */
  }

  onPageChanged = (pageNumber) => {
    let {pageSize} = this.props;
    this.props.setCurrentPageContainer(pageNumber);

    this.props.getUsersThunkCreator(pageNumber, pageSize);

    /* this.props.toggleIsFetchingContainer(true);

    usersAPI.getUsers(pageNumber, this.props.pageSize)
      .then((data) => {
        this.props.toggleIsFetchingContainer(false);
        this.props.setUssersContainer(data.items);
      }); */
  };

  render() {
    return (
      <>
        {this.props.isFetching ? <Preloader/> : null}
        <Users
          totalUsersCount={this.props.totalUsersCount}
          pageSize={this.props.pageSize}
          currentPage={this.props.currentPage}
          users={this.props.users}
          onPageChanged={this.onPageChanged}
          isFetching={this.props.isFetching}
          followingInProgress={this.props.followingInProgress}
          followThunk={this.props.followThunk}
          unFollowThunk={this.props.unFollowThunk}
        />
      </>
    );
  }
}

