/* const UsersContainer = connect(mapStateToProps, mapDispatchToProps)(UsersAPIContainer); */

export default compose(
  connect(mapStateToProps, 
    {
        setCurrentPageContainer: setCurrentPageAC,
        getUsersThunkCreator,
        unFollowThunk,
        followThunk,
    }
  ))(UsersAPIContainer);
