const ADD_MESSAGE = "ADD-MESSAGE";

let initialState = {
  dialogs: [
    { id: 1, name: "Dimych" },
    { id: 2, name: "Andrey" },
    { id: 3, name: "Sveta" },
    { id: 4, name: "Sasha" },
    { id: 5, name: "Valera" },
    { id: 6, name: "Pety" },
  ],

  messages: [
    { id: 1, message: "Hi" },
    { id: 2, message: "How is yor it" },
    { id: 3, message: "Yo" },
    { id: 4, message: "Yo" },
  ],
};

let stateNew;

const dialogsReducer = (state = initialState, action) => {
    switch(action.type) {
        case ADD_MESSAGE: 
            _addMessage(state, action);
            return stateNew;
        default: return state;
    }
};

//   
const _addMessage = (state, action) => {
  let newMessage = {
    id: 5,
    message: action.newMessageBody,
  };
  let stateCopy = {
      ...state,
      messages: [...state.messages, newMessage],
  };
  return stateNew = stateCopy;
};
//   

//      TEXTAREA  ,    
//  

//  action   
export const addMessageActionCreater = (newMessageBody) => ({type: ADD_MESSAGE, newMessageBody})
//  action  end

//  action   
//  action  end


export default dialogsReducer;