import { stopSubmit } from "redux-form";
import { authAPI, securityAPI } from "../api/api";

const SET_USER_DATA = "auth/SET_USER_DATA";
const GET_CAPTCHA_URL_SUCCESS = "GET_CAPTCHA_URL_SUCCESS";

let initialState = {
  id: null,
  email: null,
  login: null,
  isAuth: false,
  captchaUrl: null, // if null then captcha is not required
};

const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_USER_DATA:
      return { ...state, ...action.payload,};
    case GET_CAPTCHA_URL_SUCCESS:
      return { ...state, ...action.payload,};
    default:
      return state;
  }
};

//  action   
export const setAuthUserDataAC = (id, email, login, isAuth) => ({
  type: SET_USER_DATA,
  payload: {
    id,
    email,
    login,
    isAuth,
  },
});

export const getCaptchaUrlSuccessAC = (captchaUrl) => ({
  type: GET_CAPTCHA_URL_SUCCESS,
  payload: {captchaUrl},
});
//  action  end

// thunk start

export const getAuthUserDataTC = () => {
  return async (dispatch) => {
    const response = await authAPI.me();
    if (response.data.resultCode === 0) {
      let { id, email, login } = response.data.data;
      dispatch(setAuthUserDataAC(id, email, login, true));
    }
    console.log(response.data);
  };
};


export const loginTC = (email, password, rememberMe, captcha) => {
  return async (dispatch) => {
    const response = await authAPI.login(email, password, rememberMe, captcha);
      if(response.data.resultCode === 0) {
        dispatch(getAuthUserDataTC());
      } else  {
        if (response.data.resultCode === 10) {
          dispatch(getCaptchaUrlTC())
        }
        let massege = response.data.messages.length > 0 ? response.data.messages[0] : "Error symbols"
        dispatch(stopSubmit("login", {_error: massege}))
      }
      console.log(response.data);
  };
};

export const getCaptchaUrlTC = () => {
  return async (dispatch) => {
    const response = await securityAPI.getCaptchaUrl();
    const captchaUrl = response.data.url;
    dispatch(getCaptchaUrlSuccessAC(captchaUrl))
  };
};

export const logOutTC = () => {
  return async (dispatch) => {
    const response = await authAPI.logOut();
      if(response.data.resultCode === 0) {
        dispatch(setAuthUserDataAC(null, null, null, false));
      }
      console.log(response.data);
  };
};

// thunk end

export default authReducer;
