import { stopSubmit } from "redux-form";
import { usersAPI, profileAPI } from "../api/api";

const ADD_POST = "ADD-POST";
const SET_USER_PROFILE = "SET_USER_PROFILE";
const SET_USER_STATUS = "SET_USER_STATUS";
const DELETE_POST = "DELETE_POST";
const SAVE_PHOTO_SUCCESS = "SAVE_PHOTO_SUCCESS";

let initialState = {
  posts: [
    { id: 1, message: "Hi, how are you?", lieksCount: 12 },
    { id: 2, message: "its my firtst message", lieksCount: 21 },
    { id: 3, message: "Yo", lieksCount: 1 },
    { id: 4, message: "Yo", lieksCount: 3 },
  ],
  newPostText: "It-project",
  profile: null,
};

let stateNew;

const profileReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_POST:
      _addPost(state, action);
      return stateNew;
    case SET_USER_PROFILE:
      return {...state, profile: action.profile};
    case SET_USER_STATUS:
      return {...state, status: action.status};
    case DELETE_POST:
      return {...state, posts: state.posts.filter(p => p.id !== action.postId)};
    case SAVE_PHOTO_SUCCESS:
      return {...state, profile: {...state.profile, photos: action.photos}};
    default:
      return state;
  }
};

//   
const _addPost = (state, action) => {
  let newPost = {
    id: 5,
    message: action.newPostText,
    lieksCount: 0,
  };
  let stateCopy = {
    ...state,
    posts: [...state.posts, newPost],
  };
  return stateNew = stateCopy;
};
//   

//  action   
export const addPostActionCreater = (newPostText) => ({ type: ADD_POST, newPostText });

export const setUserProfileAC = (profile) => ({type: SET_USER_PROFILE, profile})
export const setUserStatuseAC = (status) => ({type: SET_USER_STATUS, status})

export const deletePostAC = (postId) => ({type: DELETE_POST, postId})
export const savePhotSuccessAC = (photos) => ({type: SAVE_PHOTO_SUCCESS, photos})
//  action  end

//thunk start

export const getUserProfileTC = (userId) => {
  return async (dispatch) => {
    const response = await usersAPI.getProfile(userId);
      dispatch(setUserProfileAC(response.data))
  }
}

export const getStatusTC = (userId) => {
  return async (dispatch) => {
    const response = await profileAPI.getStatus(userId)
      dispatch(setUserStatuseAC(response.data))
  }
}

export const updateStatusTC = (status) => {
  return async (dispatch) => {
    const response = await profileAPI.updateStatus(status)
      if(response.data.resultCode === 0) {
        dispatch(setUserStatuseAC(status))
      }
  }
}

export const savePhotoTC = (file) => {
  return async (dispatch) => {
    const response = await profileAPI.savePhoto(file)
      if(response.data.resultCode === 0) {
        dispatch(savePhotSuccessAC(response.data.data.photos))
      }
  }
}

export const saveProfileTC = (profile) => {
  return async (dispatch, getState) => {
    const userId = getState().auth.id;
    const response = await profileAPI.saveProfile(profile)
      if(response.data.resultCode === 0) {
        dispatch(getUserProfileTC(userId))
      } else {
        dispatch(stopSubmit("edit-profile", {_error: response.data.messages[0]}))
        return Promise.reject(response.data.messages[0])
      }
  }
}

//thunk end

export default profileReducer;
