import React from "react";
import { useState } from "react";
import s from "./Paginator.module.css";

let Paginator = (props) => {
  let pagesCount = Math.ceil(props.totalItemsCount / props.pageSize);
  let portionSize = 10
  let pages = [];

  for (let i = 1; i <= pagesCount; i++) {
    pages.push(i);
  }

  let portionCount = Math.ceil(pagesCount / portionSize);
  let [portionNumber, setPortionNumber] = useState(1);
  let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
  let rightPortionPageNumber = portionNumber * portionSize;

  console.log("portionNumber =" + " " + portionNumber)
  console.log("leftPortionPageNumber =" + " " + leftPortionPageNumber)
  console.log("rightPortionPageNumber =" + " " + rightPortionPageNumber)
  console.log(pages.filter(p => p >= leftPortionPageNumber && p <= rightPortionPageNumber)); 




  return (
    <div className={s.paginator}>
      { portionNumber > 1 &&
      <button onClick={ () => { setPortionNumber(portionNumber - 1)}}>PREV</button>}
      {pages
        .filter(p => p >= leftPortionPageNumber && p <=rightPortionPageNumber)
        .map((p) => {
          return (
            <span
              onClick={(e) => {
                props.onPageChanged(p);
              }}
              className={props.currentPage === p ? s.selectadPage : ""}
            >
              {p}
            </span>
          );
      })}
      { portionCount > portionNumber &&
      <button onClick={ () => { setPortionNumber(portionNumber + 1)}}>Next</button>}

    </div>
  );
};

export default Paginator;
