import { usersAPI } from "../api/api";

const FOLLOW = "FOLLOW";
const UNFOLLOW = "UNFOLLOW";
const SET_USERS = "SET_USERS";
const SET_CURRENT_PAGE = "SET_CURRENT_PAGE";
const TOTAL_USERS_COUNT = "TOTAL_USERS_COUNT";
const TOGGLE_IS_FETCHING = "TOGGLE_IS_FETCHING";
const TOGGLE_IS_FOLLOWING_PROGRESS = "TOGGLE_IS_FOLLOWING_PROGRESS";

let initialState = {
  users: [],
  pageSize: 5,
  totalUsersCount: 0,
  currentPage: 1,
  isFetching: false,
  followingInProgress: [],
};

let stateNew;

const usersReducer = (state = initialState, action) => {
  switch (action.type) {
    case FOLLOW:
      _followSub(state, action);
      return stateNew;
    case UNFOLLOW:
      _unfollow(state, action);
      return stateNew;
    case SET_USERS:
      return { ...state, users: action.users };
    case SET_CURRENT_PAGE:
      return { ...state, currentPage: action.currentPage };
    case TOTAL_USERS_COUNT:
      return { ...state, totalUsersCount: action.totalUsersCount };
    case TOGGLE_IS_FETCHING:
      return { ...state, isFetching: action.isFetching };
    case TOGGLE_IS_FOLLOWING_PROGRESS:
      return {
        ...state,
        followingInProgress: action.isFetching
          ? [...state.followingInProgress, action.userId]
          : state.followingInProgress.filter((id) => id !== action.userId),
      };
    default:
      return state;
  }
};

const following = (state, action, boolian) => {
  let stateCopy = {
    ...state,
    users: state.users.map((u) => {
      if (u.id === action.userId) {
        return { ...u, followed: boolian };
      }
      return u;
    }),
  };
  return (stateNew = stateCopy);
}

//   
const _followSub = (state, action) => {
  let boolian = true;
  following(state, action, boolian)
};
//   

//      TEXTAREA,    
const _unfollow = (state, action) => {
  let boolian = false;
  following(state, action, boolian)
};
//  

//  action   
export const followAC = (userId) => ({ type: FOLLOW, userId });
export const unfollowAC = (userId) => ({ type: UNFOLLOW, userId });
export const setUssersAC = (users) => ({ type: SET_USERS, users });
export const setCurrentPageAC = (currentPage) => ({
  type: SET_CURRENT_PAGE,
  currentPage: currentPage,
});
export const setTotalUsersCountAC = (count) => ({
  type: TOTAL_USERS_COUNT,
  totalUsersCount: count,
});
export const toggleIsFetchingAC = (isFetching) => ({
  type: TOGGLE_IS_FETCHING,
  isFetching,
});
export const toggleFollowingProgressAC = (isFetching, userId) => ({
  type: TOGGLE_IS_FOLLOWING_PROGRESS,
  isFetching,
  userId,
});
//  action  end

// thunk start

export const getUsersThunkCreator = (currentPage, pageSize) => {
  return async (dispatch) => {
    dispatch(toggleIsFetchingAC(true));

    const data = await usersAPI.getUsers(currentPage, pageSize);
    dispatch(toggleIsFetchingAC(false));
    dispatch(setUssersAC(data.items));
    dispatch(setTotalUsersCountAC(data.totalCount));
    console.log(data);
  };
};

const followUnfollowFlow = async (dispatch, userId, apiMethod, actionCreator) => {
  dispatch(toggleFollowingProgressAC(true, userId));
  const response = await apiMethod(userId);
  if (response.data.resultCode === 0) {
    dispatch(actionCreator(userId));
  }
  dispatch(toggleFollowingProgressAC(false, userId));
};

export const followThunk = (userId) => {
  return async (dispatch) => {
    let apiMethod = usersAPI.follow.bind(usersAPI);
    let actionCreator = followAC;

    followUnfollowFlow(dispatch, userId, apiMethod, actionCreator);
  };
};

export const unFollowThunk = (userId) => {
  return async (dispatch) => {
    let apiMethod = usersAPI.unFollow.bind(usersAPI);
    let actionCreator = unfollowAC;

    followUnfollowFlow(dispatch, userId, apiMethod, actionCreator);
  };
};

// thunk end

export default usersReducer;
