import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { required } from "../../utils/validators/validators";
import { creatField } from "../common/FormsControls/FormsControls";
import { Input } from "../common/FormsControls/FormsControls";
import { loginTC } from "../../redux/auth-reducer";
import { Navigate } from "react-router-dom";
import s from "../common/FormsControls/FormsControls.module.css"

const LoginForm = (props) => {
  console.log(props);
  return (
    <div>
      <form onSubmit={props.handleSubmit} action="">
        <div>
          {creatField("Email", "email", required, Input, "text")}
          {/* <Field
            name="email"
            placeholder="email"
            component={Input}
            type="text"
            validate={[required]}
          /> */}
        </div>
        <div>
          {creatField("Password", "password", required, Input, "password")}
          {/* <Field
            name="password"
            placeholder="passsword"
            component={Input}
            type="password"
            validate={[required]}
          /> */}
        </div>
        <div>
          <Field name="rememberMe" component="input" type="checkbox" /> remember
          me
        </div>
        {props.captchaUrl && <img src={props.captchaUrl} alt="captcha" />}
        {props.captchaUrl && creatField("Symbols from image", "captcha", required, Input, "text")}
        { props.error && <div className={s.formSummareError}>
          {props.error}
        </div>}
        <div>
          <button>Login</button>
        </div>
      </form>
    </div>
  );
};

const LoginReduxForm = reduxForm({
  form: "login",
})(LoginForm);

const Login = (props) => {
  const onSubmit = (formData) => {
    props.loginTC(formData.email, formData.password, formData.rememberMe, formData.captcha)
  };
  if(props.isAuth) {
    return (
      <Navigate to={"/profile"}/>
    )
  }

  return (
    <div>
      <h1>Login</h1>
      <LoginReduxForm onSubmit={onSubmit} captchaUrl={props.captchaUrl}/>
    </div>
  );
};

const mapStateToProps = (state) => ({
  captchaUrl: state.auth.captchaUrl,
  isAuth: state.auth.isAuth,
})

export default connect(mapStateToProps, {loginTC})(Login);
